@import url("https://fonts.googleapis.com/css?family=Open+Sans");

* {
  box-sizing: border-box;
}

body {
  background-color: rgb(97, 91, 13) !important;
  padding: 20px;
  font-family: "Open Sans", serif;
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  margin: 0;
}

#main {
  display: flex;
  flex: 1;
  flex-direction: column;
  box-shadow: 10px 5px 5px red;
  border-radius: 10px;
  max-width: 850px;
}
.form {
  flex: 1;
  background: rgb(250, 250, 0);
  max-width: 570px;
  border-radius: 0 0 5px 5px;
}
.info {
  border-radius: 5px 5px 0 0;
  background: rgb(24, 140, 218);
  order: -1;
  min-width: 300px;
  text-shadow: 1px 1px 2px yellow;
  font-weight: bold;
}

.form,
.info {
  padding: 1em;
}

.weather {
  padding-top: 30px;
}

.error {
  font-weight: bold;
}

@media screen and (min-width: 576px) {
  #main {
    flex-direction: row;
  }
  .info {
    flex: 0 0 20vw;
    border-radius: 5px 0 0 5px;
  }
  .form {
    border-radius: 0 5px 5px 0;
  }
}

input[type="text"] {
  background-color: transparent;
  border: 0;
  border-bottom: solid 2px #113a65;
  width: 50%;
  padding-bottom: 4px;
  color: #113a65;
  font-weight: normal;
  margin-bottom: 30px;
  margin-right: 20px;
  font-size: 20px;
  outline: none;
}
input::-webkit-input-placeholder {
  color: #103a65;
}
button {
  border: 2px solid #103a65;
  border-bottom: 4px solid #103a65;
  padding: 8px 20px;
  margin: 0 2px;
  border-radius: 2px;
  cursor: pointer;
  background-color: #235d9c;
  color: #fff;
  outline: none !important;
}

button:hover {
  border-bottom: 2px solid #103a65;
  position: relative;
  top: 2px;
}

button:active {
  border-bottom: 2px solid transparent;
  border-color: transparent;
  position: relative;
  top: 2px;
}
